@import url('https://fonts.googleapis.com/css?family=Maven+Pro&display=swap');
.wrapper {
  
  font-family: 'Maven Pro', sans-serif;
  color: white;
}

#profile {
  background-color: black;
  margin-bottom: 10%;
  margin-top: 10vh;
  border-top: white solid 1px;
  border-bottom: white solid 1px;
  display: grid;
  grid-template-columns: auto auto auto auto auto auto auto;
  grid-template-rows: auto auto auto;
  
}

.profile-section {
  padding: 10px;
  
  font-size: 20px;
  border: white 1px solid;
}

.section0 {
  grid-column-start: 1;
  grid-column-end: 8;
  grid-row-start: 1;
  grid-row-end: 2;

  display: flex;
  flex-direction: row;
  justify-content: space-around;
}


.section2 {
  grid-column-start: 6;
  grid-column-end: 8;
  grid-row-start: 2;
  grid-row-end: 4;
}

.section3 {
  grid-column-start: 1;
  grid-column-end: 6;
  grid-row-start: 3;
  grid-row-end: 4;
}

.subscribe-btn {
  background-color: gray;
  border: black;
  border-radius: 10px;
  height: 2.5em;
  margin: auto;
  color: white;
}

.subscribe-btn:hover {
  background-color: darkslategrey;
  cursor: pointer;
  
  transition: background-color 1s linear;
}

#recommended-profile {
  background-color: black;
  margin-bottom: 10%;
  
  border-top: white solid 1px;
  border-bottom: white solid 1px;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: 50% 50%;
}

#recommended-profile-2 {
  background-color: black;
  margin-bottom: 10%;
  
  border-top: white solid 1px;
  border-bottom: white solid 1px;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: 100%;
}

button {
  border-radius: 10px;
  border: 1px solid black;
  color: white;
  background-color: gray;
  height: 5vh;
  cursor: pointer;
}

.recommended-item {
  border: 1px solid white;
  padding: 5px;
  
}