@import url('https://fonts.googleapis.com/css?family=Maven+Pro&display=swap');
.wrapper {
  
  font-family: 'Maven Pro', sans-serif;
  color: white;
}

#recommended-video {
  background-color: black;
  margin-bottom: 10%;
  margin-top: 10vh;
  border-top: white solid 1px;
  border-bottom: white solid 1px;
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto auto;
}

.recommended-item {
  border: 1px solid white;
  padding: 5px;
}

#vid {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 2;
}

#suggestions {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 4;

  
}

#description {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
}

#comments {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 3;
  grid-row-end: 4;
}

.suggested-item {
  border-bottom: 1px white solid;
  padding: 5px;
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto;
  grid-column-gap: 10px;
}

.subscribe-btn {
  background-color: gray;
  border: black;
  border-radius: 10px;
  height: 2.5em;
  margin: auto;
  color: white;
}

.subscribe-btn:hover {
  background-color: darkslategrey;
  cursor: pointer;
  
  transition: background-color 1s linear;
}

.comment {
  padding: 5px;
  border-bottom: 1px white solid;
}

textarea {
  border-radius: 10px;
  background-color: #252830;
  color: white;
}

.profile-link:hover {
  color: gray;
}