@import url('https://fonts.googleapis.com/css?family=Maven+Pro&display=swap');

.wrapper {
  font-family: 'Maven Pro', sans-serif;
  color: white;
}

#recommended-upload {
  background-color: black;
  margin-bottom: 11%;
  margin-top: 10vh;
  border-top: white solid 1px;
  border-bottom: white solid 1px;
  height: 60vh;
  display: grid;
  grid-template-rows: auto auto auto auto auto;
  align-items: center;
}

.recommended-item {
  border: 1px solid white;
  padding: 10px;
}

input[id="vid-upload"] {
  display: none;
}


.choose-vid {
  
  border: 1px solid white;
  color: white;
  display: inline-block;
  cursor: pointer;
  padding: 10px;
  margin: 10px;
}

input[id="thumbnail-upload"] {
  display: none;
}


.choose-pic {
  
  border: 1px solid white;
  color: white;
  display: inline-block;
  cursor: pointer;
  padding: 10px;
  margin: 10px;
}

#title-input {
  height: 2.5rem;
  font-size: 1.5rem;
  padding: 1px;
  margin: 10px;
  background-color: #252830;
  border: black 1px solid;
  border-radius: 10px;
  color: white;

}

#description-input {
  color: white;
  background-color: #252830;
  border: 1px solid black;
  border-radius: 10px;
  margin: 3px;
}

.recommended-item button {
  border-radius: 10px;
  border: 1px solid black;
  color: white;
  background-color: gray;
  height: 5vh;
}

button {
  cursor: pointer;
}